import React, { useRef, useState } from 'react';
import {QrReader} from 'react-qr-reader'


const Test = () => {
    const [scanResult, setscanResult] = useState(null);
  const [error, seterror] = useState(null);
  return (
    <div style={{
      display:'flex',
      flexDirection:"column",
      justifyContent:"center",
      alignItems:"center",
      height:'100vh'
    }} className="">
      <div style={{ width:"calc(350px)", height:"350px", border:"1px solid black" }}>
      <h3>qr code scann by webcam</h3>
      {
        error ? error?.toString()+" +"
        : "no error**"
      }
      <QrReader
          // delay={300}
          facingMode="environment"
          style={{width:"100%"}}
          // constraints={ 
          //   // {facingMode: "user" 
          // }}
          onError={(error)=>
          {
            seterror(error)
          }}
          onScan={(result)=>{
            console.log(result)
            setscanResult(result)
          }}
          />
        <p>{scanResult?.text}</p>
    </div>
    </div>
  )
}

export default Test